.nav {
  display: flex;
  flex-direction: row;
  padding: 1rem;
  width: 100%;

  // font-md
  font-size: 21px;
  letter-spacing: 0.015em;
  line-height: 1.3;
}

.nav__right {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;

  .nav__link-span {
    margin-left: 0.25em;
  }
}

.nav__link-span {
  background-color: var(--white);
  display: inline-block;
  padding: 0.25em;

  &.active {
    -webkit-font-smoothing: antialiased;
    background-color: var(--black);

    .nav__link {
      border-bottom: none;
      color: var(--white) !important;
    }
  }
}

.nav__link {
  color: var(--black) !important;
}
